import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { BehaviorSubject, Observable } from 'rxjs';
import { API_ENDPOINTS, API_URL } from 'src/app/Model/Global';
import { BaseApiHandlerService } from '../base-api-handler.service';

import { User } from 'src/app/Model/User';
import { Role } from 'src/app/Model/Role';

@Injectable({
  providedIn: 'root'
})
export class UserService extends BaseApiHandlerService {
  // DeleteUser(id: string) {
  //   throw new Error('Method not implemented.');
  // }
  userObserve$:Observable<User[]>;
  private userSubject$: BehaviorSubject<User[]> = new BehaviorSubject<User[]>(undefined);

  roleObservable$:Observable<Role[]>;
  private roleSubject$: BehaviorSubject<Role[]> = new BehaviorSubject<Role[]>(undefined);
  public get User(){ return this.userSubject$.value}

  loggedInUserObservable$:Observable<User>;
  private loggedInUserSubject$: BehaviorSubject<User> = new BehaviorSubject<User>(undefined);

  constructor(private http:HttpClient) {
    super(http);
    this.userObserve$= this.userSubject$.asObservable();
    this.roleObservable$=this.roleSubject$.asObservable();
    this.loggedInUserObservable$= this.loggedInUserSubject$.asObservable();
  }

  SaveUser(data){
    return this.Post(data,API_URL+API_ENDPOINTS.CreateTeamMember);
  }
  UpdateUser(data:User){
    return this.Update(data.id,data,API_URL+API_ENDPOINTS.UpdateTeamMember);
  }
  GetUser(){
    return this.GetAll(API_URL+API_ENDPOINTS.TeamMember).subscribe(res =>{
      this.userSubject$.next(res.dynamicResult);
    });
  }
  DeleteUser(id) {
     return this.Delete(id,API_URL+API_ENDPOINTS.DeleteTeamMember);
   }

    GetRoles()
   {
     return this.Get(API_URL+API_ENDPOINTS.Roles+"/GetAllRoles").subscribe(res=>{
       this.roleSubject$.next(res.dynamicResult);
     });
   }
   GetLoginUser(){
    return this.GetAll(API_URL+API_ENDPOINTS.Auth+'/GetLoggedInUser').subscribe(res=>{
      this.loggedInUserSubject$.next(res.dynamicResult);
    })
   }
}
